		finddata(de,pa){
			if(uni.getStorageSync('landRegist')){
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				let params = {
					page:pa,
					userId:landRegistLG.user.id,
					type:de
				}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/adve/findList', //接口地址。
					data: params,
					method: 'POST',
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						console.log(response.data.content);
						// this.tabBars=response.data.content.allChannel
						// this.newsList[5].data=response.data.content.compService.list;
						response.data.content.allChannel.map((item,index)=>{
							let obj={
								name:item.typeName,
								id:item.type,
							}
							this.tabBars.push(obj);
						})
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}else if (uni.getStorageSync('UUID')) {
				let UUID = uni.getStorageSync('UUID'); // 读取缓存的用户信息
				console.log(UUID);
				let params = {
					page:'1',
					userId:UUID,
					type:'inst'
				}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/adve/findList', //接口地址。
					data: params,
					method: 'POST',
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						this.newsList.data=response.data.content
						this.tabBars=response.data.content.allChannel
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
