		cliskShowPingDao() {
			console.log('触发展示频道组件');
			this.pinDaoShow = !this.pinDaoShow; // 展示频道
		},
		cliskClosePingDao() {
			console.log('触发关闭频道组件');
			this.pinDaoShow = !this.pinDaoShow; // 关闭频道频道
			this.editOrFinish = 0;
		},
		pinDaoEdit() {
			console.log('触发编辑按钮');
			this.editOrFinish = 1;
		},
		pinDaoFinish() {
			console.log('触发完成按钮');
			this.editOrFinish = 0;
		},
		pinDaoDelete(e) {
			console.log(e, '触发删除频道子项X按钮');
			this.tabBarsAdd.unshift(this.tabBars[e]); // 向添加频道添存储我的的频道已删除的子项
			delete this.tabBars.splice(e, 1); // 删除我的频道选中的子项
			console.log(this.tabBars, '--------------this.tabBars----------------');
		},
		pinDaoAdd(e) {
			console.log('触发频道添加子项');
			this.tabBars.push(this.tabBarsAdd[e]); // 向我的频道添加新增加的频道子项
			delete this.tabBarsAdd.splice(e, 1); // 删除添加频道中被选中的子项
		},
		getList(index) {
			let activeTab = this.newsList[index];
			let list = [];
			for (let i = 1; i <= 10; i++) {
				let item = Object.assign({}, newsData['data' + Math.floor(Math.random() * 5)]);
				item.id = this.newGuid();
				list.push(item);
			}
			activeTab.data = activeTab.data.concat(list);
			console.log(activeTab.data, '----------------activeTab.data----------------');
		},
		close(index1, index2) {
			uni.showModal({
				content: '是否删除本条信息？',
				success: res => {
					if (res.confirm) {
						this.newsList[index1].data.splice(index2, 1);
					}
				}
			});
		},
		loadMore(e) {
			// setTimeout(() => {
			// 	this.getList(this.tabIndex);
			// }, 500);
			console.log(this.Currentpage)
			if(this.Currentpage==5){
				this.default='compService';
				this.pages++;
				this.finddatas(this.default,this.pages);
			}
		},
		ontabtap(e) {
			this.pinDaoShow = false; // 关闭频道频道
			this.editOrFinish = 0;
			let index = e.target.dataset.current || e.currentTarget.dataset.current;
			let entry = 2; // 进入swiper方式, 1时动画为0， 2时动画300毫秒
			this.switchTab(index, entry); //
		},
		ontabtapItems(e) {
			this.pinDaoShow = false; // 关闭频道频道
			this.editOrFinish = 0;
			let index = e.target.dataset.current || e.currentTarget.dataset.current;
			let entry = 1; // 进入swiper方式, 1时动画为0， 2时动画300毫秒
			this.switchTab(index, entry); // entry = 1; // 进入swiper方式, 1时动画为0， 2时动画300毫秒
		},
		ontabchange(e) {
			let index = e.target.current || e.detail.current;
			let entry = 2; // 进入swiper方式, 1时动画为0， 2时动画300毫秒
			this.switchTab(index, entry);
			
			this.Currentpage=e.detail.current;
			console.log(this.Currentpage)
			if(e.detail.current==0){
				this.default='follow';
				this.pages='1';
				this.finddatass(this.default,this.pages);
			}else if(e.detail.current==1){
				this.default='push';
				this.pages='1';
				this.finddatass(this.default,this.pages);
			}else if(e.detail.current==2){
				this.default='inst';
				this.pages='1';
				this.finddatass(this.default,this.pages);
			}else if(e.detail.current==3){
				this.default='purc';
				this.pages='1';
				this.finddatass(this.default,this.pages);
			}else if(e.detail.current==4){
				this.default='news';
				this.pages='1';
				this.finddatass(this.default,this.pages);
			}else if(e.detail.current==5){
				this.default='compService';
				this.pages='1';
				this.finddatass(this.default,this.pages);
			}else if(e.detail.current==6){
				this.default='invest';
				this.pages='1';
				this.finddatass(this.default,this.pages);
			}
		},
		switchTab(index, time) {
			if (time === 1) {
				// 进入swiper方式, 1时动画为0， 2时动画300毫秒
				this.durationTime = 0; // swiper动画时间
			} else {
				this.durationTime = 300; // swiper动画时间
			}
			// if (this.newsList[index].data.length === 0) {
			// 	// this.getList(index);
			// }

			if (this.tabIndex === index) {
				return;
			}

			// 缓存 tabId
			// if (this.newsList[this.tabIndex].data.length > MAX_CACHE_DATA) {
			// 	let isExist = this.cacheTab.indexOf(this.tabIndex);
			// 	if (isExist < 0) {
			// 		this.cacheTab.push(this.tabIndex);
			// 		//console.log("cache index:: " + this.tabIndex);
			// 	}
			// }
			this.tabIndex = index;
			this.scrollInto = this.tabBars[index].id;

			// 释放 tabId
			if (this.cacheTab.length > MAX_CACHE_PAGE) {
				let cacheIndex = this.cacheTab[0];
				this.clearTabData(cacheIndex);
				this.cacheTab.splice(0, 1);
				//console.log("remove cache index:: " + cacheIndex);
			}
		},
		clearTabData(e) {
			this.newsList[e].data.length = 0;
			this.newsList[e].loadingText = '加载更多...';
		},
		refreshData() {},
		onrefresh(e) {
			var tab = this.newsList[this.tabIndex];
			if (!tab.refreshFlag) {
				return;
			}
			tab.refreshing = true;
			tab.refreshText = '正在刷新...';

			setTimeout(() => {
				this.refreshData();
				this.pulling = true;
				tab.refreshing = false;
				tab.refreshText = '已刷新';
				setTimeout(() => {
					// TODO fix ios和Android 动画时间相反问题
					this.pulling = false;
				}, 500);
			}, 2000);
		},
		onpullingdown(e) {
			var tab = this.newsList[this.tabIndex];
			if (tab.refreshing || this.pulling) {
				return;
			}
			if (Math.abs(e.pullingDistance) > Math.abs(e.viewHeight)) {
				tab.refreshFlag = true;
				tab.refreshText = '释放立即刷新';
			} else {
				tab.refreshFlag = false;
				tab.refreshText = '下拉可以刷新';
			}
		},
		newGuid() {
			let s4 = function() {
				return ((65536 * (1 + Math.random())) | 0).toString(16).substring(1);
			};
			return (s4() + s4() + '-' + s4() + '-4' + s4().substr(0, 3) + '-' + s4() + '-' + s4() + s4() + s4()).toUpperCase();
		}
	}
};
